import React, { useState } from 'react';
import { usePapaParse } from 'react-papaparse';
import Plot from 'react-plotly.js';
import DataTable from 'react-data-table-component';

const Dashboard = () => {
  const [data, setData] = useState([]);
  const [headers, setHeaders] = useState([]);
  const [selectedDimension, setSelectedDimension] = useState('');
  const [selectedMeasure, setSelectedMeasure] = useState('');
  const [chartType, setChartType] = useState('Bar');
  const [chartWidth, setChartWidth] = useState(900);
  const [chartHeight, setChartHeight] = useState(600);
  const [chartTitle, setChartTitle] = useState('Chart');
  const [showData, setShowData] = useState(false);
  const { readString } = usePapaParse();

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    const reader = new FileReader();

    reader.onload = ({ target }) => {
      const csv = target.result;
      readString(csv, {
        header: true,
        complete: (result) => {
          const headers = Object.keys(result.data[0]);
          setHeaders(headers);
          setData(result.data);
        },
      });
    };

    reader.readAsText(file);
  };

  const handleDimensionChange = (event) => {
    setSelectedDimension(event.target.value);
  };

  const handleMeasureChange = (event) => {
    setSelectedMeasure(event.target.value);
  };

  const handleChartTypeChange = (event) => {
    setChartType(event.target.value);
  };

  const handleWidthChange = (event) => {
    setChartWidth(event.target.value);
  };

  const handleHeightChange = (event) => {
    setChartHeight(event.target.value);
  };

  const handleTitleChange = (event) => {
    setChartTitle(event.target.value);
  };

  const handleShowDataChange = (event) => {
    setShowData(event.target.checked);
  };

  const renderChart2 = () => {
    if (!selectedDimension || !selectedMeasure) return null;

    const x = data.map(item => item[selectedDimension]);
    const y = data.map(item => item[selectedMeasure]);

    let chartData = [{
      x: x,
      y: y,
      type: chartType.toLowerCase(),
      mode: chartType === 'Scatter' ? 'markers' : undefined,
      marker: { color: 'blue' },
    }];

    if (chartType === 'Pie' || chartType === 'Donut') {
      chartData = [{
        labels: x,
        values: y,
        type: 'pie',
        hole: chartType === 'Donut' ? 0.4 : 0,
      }];
    }

    return (
      <Plot
        data={chartData}
        layout={{ width: Number(chartWidth), height: Number(chartHeight), title: chartTitle }}
      />
    );
  };

  const renderChart = () => {
    if (!selectedDimension || !selectedMeasure) return null;
  
    const x = data.map(item => item[selectedDimension]);
    const y = data.map(item => item[selectedMeasure]);
  
    let chartData = [{
      x: x,
      y: y,
      type: chartType.toLowerCase(),
      mode: chartType === 'Scatter' ? 'markers' : undefined,
      marker: { color: 'blue' },
      hovertemplate: `%{x}<br>%{y}<extra></extra>`, // Correctly displays x and y values
    }];
  
    if (chartType === 'Pie' || chartType === 'Donut') {
      chartData = [{
        labels: x,
        values: y,
        type: 'pie',
        hole: chartType === 'Donut' ? 0.4 : 0,
        hovertemplate: `%{label}: %{value}<extra></extra>`, // Custom hover for pie/donut
      }];
    }
  
    return (
      <Plot
        data={chartData}
        layout={{ width: Number(chartWidth), height: Number(chartHeight), title: chartTitle }}
      />
    );
  };
  

  const renderDataTable = () => {
    const columns = headers.map(header => ({
      name: header,
      selector: row => row[header],
      sortable: true,
    }));

    return (
      <DataTable
        columns={columns}
        data={data}
        pagination
        className="shadow-md border border-gray-200 rounded-lg"
      />
    );
  };

  return (
    <div className="p-8 bg-gray-100 min-h-screen">
      <div className="mb-6">
        <input 
          type="file" 
          accept=".csv" 
          onChange={handleFileUpload} 
          className="block w-full text-sm text-gray-500 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-blue-50 file:text-blue-700 hover:file:bg-blue-100" 
        />
      </div>
      {headers.length > 0 && (
        <>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4 mb-6">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Dimension</label>
              <select
                className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                value={selectedDimension}
                onChange={handleDimensionChange}
              >
                <option value="" disabled>Select Dimension</option>
                {headers.map((header, idx) => (
                  <option key={idx} value={header}>
                    {header}
                  </option>
                ))}
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Measure</label>
              <select
                className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                value={selectedMeasure}
                onChange={handleMeasureChange}
              >
                <option value="" disabled>Select Measure</option>
                {headers.filter(header => header !== selectedDimension).map((header, idx) => (
                  <option key={idx} value={header}>
                    {header}
                  </option>
                ))}
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Chart Type</label>
              <select
                className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                value={chartType}
                onChange={handleChartTypeChange}
              >
                <option value="Bar">Bar</option>
                <option value="Pie">Pie</option>
                <option value="Donut">Donut</option>
                <option value="Scatter">Scatter</option>
              </select>
            </div>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Chart Width (px)</label>
              <input
                type="number"
                value={chartWidth}
                onChange={handleWidthChange}
                className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Chart Height (px)</label>
              <input
                type="number"
                value={chartHeight}
                onChange={handleHeightChange}
                className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Chart Title</label>
              <input
                type="text"
                value={chartTitle}
                onChange={handleTitleChange}
                className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
              />
            </div>
          </div>
          <div className="mb-6">
            <label className="inline-flex items-center">
              <input 
                type="checkbox" 
                checked={showData} 
                onChange={handleShowDataChange}
                className="rounded text-indigo-600 shadow-sm focus:ring-indigo-500"
              />
              <span className="ml-2 text-sm font-medium text-gray-700">Show Data</span>
            </label>
          </div>
          {renderChart()}
          {showData && renderDataTable()}
        </>
      )}
    </div>
  );
};

export default Dashboard;
